body{
    background-color: black;
}

.App{
    width: 50%;
    margin: auto;
    margin-top:50px ;
}
.container-todo{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width:100%;
    border:1px solid salmon;
    border-radius: 10px;
    background-color: gainsboro;

}

.done{
    text-decoration: line-through;
    text-decoration-color:red ;
}

input ,button{
    margin-top: 20px;
    width:90%;
    padding: 10px 0;
    border: none;
    text-align: center;

}
button{
    color:white;
    background-color: red;
    border-radius: 10px;
    font-weight: bold;
}

